@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Rock+Salt&family=Spectral:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* For mobile */
.link a {
    text-decoration: none;
    margin-left: 10px;
    font-size: 60px;
    color: #49494b;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 200;
}

.navbar {
    padding-top: 40px;
    background: white;
    min-height: 80px !important;
}

.logoSize {
    height: 80px;
    width: auto;
    padding: 10px;
}

.toggle-icon {
    color: black;
}

.brand {
    margin-left: 30px;
    font-size: 78px;
    font-family: 'Playfair Display', serif;
    color: #49494b;
    letter-spacing: 5px;
}

.justify-content-end {
    margin-top: 19px;
    background: white;
    min-height: 80px !important;
}

/* For desktop */
@media (min-width: 1000px) {
    .link a {
        font-size: 130%;
    }

    .justify-content-end {
        margin-top: unset;
    }

    .navbar {
        padding-top: unset;
    }
}
.github a:hover {
    text-decoration: none;
    color: #49494b;
    border-bottom: 1px solid;
}

.footer {
    font-size: 20px;
}
.container {
    display: flex;
    max-width: 1300px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.img {
    position: relative;
    padding-top: 30px;
    width: 100%;
    height: 100%;
}

.center {
    padding-top: 50px;
    justify-content: center;
}

.logo {
    display: block;
    text-align: center;
    margin: 0 auto;
}

.title {
    text-align: center;
    margin: 0 auto;
    font-size: 130px;
    letter-spacing: 10px;
    font-family: 'Playfair Display', serif;
    margin-top: 10px;
}

.box {
    position: relative;
    text-align: center;
}

.imgText a {
    position: absolute;
    color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    letter-spacing: 15px;
    font-size: 100px;
    align-content: center;
}

.imgText a:hover {
    text-decoration: none;
    border-bottom: 1px solid;
}

@media (min-width: 1000px) {
    .imgText a {
        font-size: 50px;
    }

    .title {
        font-size: 80px;
    }
}
.container {
    margin-top: 0px;
    display: flex;
    width: 100vw;
    justify-content: center;
    align-items: center;
}
.about-container {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    color:#49494b;
    margin-left: 20px;
    margin-right: 20px;
}

.about-title {
    font-family: 'Playfair Display', serif;
    text-align: center;
    font-size: 60px;
}

.about-img {
    display: flex;
    justify-content: center;
}

.about-text {
    width: 700px;
    overflow: hidden;
    margin-left: 30px;
    margin-right: 30px;
}

.about-text p {
    padding-bottom: 30px;
    font-weight: 400;
}

.headshot {
    overflow: hidden;
    padding-top: 30px;
    padding-bottom: 70px;
    max-height: 800px;
    width: auto;
    margin-right: 50px;
}

@media (max-width: 1000px) {
    .about-title {
        letter-spacing: 5px;
        margin-top: 30px;
        font-size: 100px;
    }

    .about-text h2 {
        font-size: 60px;
    }

    .about-text p {
        font-size: 35px;
    }
}

